'use client'
import { useMemo, useState } from 'react'

type ModuleData = {
  id: string
  title: string
}

type ChapterData = {
  id: string
  module_id: string
  title: string
  content: string
}

type Props = {
  modules: ModuleData[]
  chapters: ChapterData[]
}

export default function ContentReader({ modules, chapters }: Props) {
  const firstChapterId = useMemo(() => chapters[0]?.id ?? null, [chapters])
  const [activeChapterId, setActiveChapterId] = useState<string | null>(firstChapterId)
  const [mobileOpen, setMobileOpen] = useState(false)
  const chaptersByModule = useMemo(() => {
    const map: Record<string, ChapterData[]> = {}
    for (const c of chapters) {
      if (!map[c.module_id]) map[c.module_id] = []
      map[c.module_id].push(c)
    }
    return map
  }, [chapters])

  const activeChapter = useMemo(
    () => chapters.find((c) => c.id === activeChapterId) ?? null,
    [chapters, activeChapterId]
  )

  const renderModulesList = () => (
    <div className="space-y-4">
      {modules.map((m) => {
        const list = chaptersByModule[m.id] ?? []
        return (
          <div key={m.id} className="rounded-md border border-border">
            <div className="px-3 py-2 font-medium">{m.title}</div>
            <div className="px-2 py-2 space-y-1">
              {list.map((c) => {
                const isActive = c.id === activeChapterId
                return (
                  <button
                    key={c.id}
                    onClick={() => {
                      setActiveChapterId(c.id)
                      setMobileOpen(false)
                    }}
                    className={
                      'w-full text-left px-3 py-2 rounded-md flex items-center gap-2 ' +
                      (isActive
                        ? 'text-white border-l-2 border-brand-red bg-background-tertiary'
                        : 'text-text-secondary hover:text-white hover:bg-background-tertiary')
                    }
                  >
                    <span>{c.title}</span>
                  </button>
                )
              })}
              {list.length === 0 ? (
                <div className="px-3 py-2 text-xs text-text-secondary">Sem capítulos</div>
              ) : null}
            </div>
          </div>
        )
      })}
    </div>
  )

  return (
    <div className="h-screen w-full overflow-hidden flex">
      <aside className="h-full w-80 overflow-hidden bg-[#0A0A0A] border-r border-border hidden md:block">
        <div className="p-4">{renderModulesList()}</div>
      </aside>
      <main className="h-full flex-1 overflow-y-auto bg-[#111111]">
        <div className="sticky top-0 z-10 bg-[#111111]/90 backdrop-blur px-6 py-3 md:hidden border-b border-border">
          <div className="flex items-center justify-between">
            <button
              onClick={() => setMobileOpen(true)}
              className="px-3 py-2 rounded-md bg-background-tertiary text-white hover:bg-background-secondary"
            >
              Módulos
            </button>
            <div className="text-text-secondary text-sm">
              {activeChapter ? activeChapter.title : 'Leitor'}
            </div>
          </div>
        </div>
        <div className="mx-auto max-w-3xl py-12 px-6">
          {activeChapter ? (
            <article className="prose prose-invert max-w-none">
              <h1>{activeChapter.title}</h1>
              <div dangerouslySetInnerHTML={{ __html: activeChapter.content }} />
            </article>
          ) : (
            <div className="text-text-secondary">Selecione um capítulo para iniciar a leitura.</div>
          )}
        </div>
      </main>
      {mobileOpen ? (
        <div className="fixed inset-0 z-50 md:hidden">
          <div
            className="absolute inset-0 bg-black/50"
            onClick={() => setMobileOpen(false)}
            aria-label="Fechar"
          />
          <div className="absolute left-0 top-0 h-full w-4/5 max-w-[20rem] bg-[#0A0A0A] border-r border-border">
            <div className="p-4 flex items-center justify-between border-b border-border">
              <div className="font-semibold">Conteúdo</div>
              <button
                onClick={() => setMobileOpen(false)}
                className="px-2 py-1 rounded-md text-text-secondary hover:text-white"
              >
                Fechar
              </button>
            </div>
            <div className="p-4 overflow-y-auto h-[calc(100%-56px)]">{renderModulesList()}</div>
          </div>
        </div>
      ) : null}
    </div>
  )
}
